* { margin:0; padding:0; box-sizing:border-box; outline:none }
body { font-size:16px; font-family: 'Goldman', cursive 'Noto Serif KR', sans-serif; }

a { color:inherit; text-decoration:none }
ul, ol {list-style:none }
.row { max-width:1600px; margin:0 auto; padding:0 80px }
.cf:after { content:''; display:block; clear:both }
img { width:100%; vertical-align:middle }
button { cursor:pointer; border:none }
fieldset { display:none }
legend { display:none }
h1, h2, h3, h4, h5, h6 { font-size:100% }
table { width:100%; border-collapse:collapse; table-layout:fixed }
.blind { position:absolute; width:1px; height:1px; overflow:hidden; text-indent:-9999px; }












